export default function DeleteAlert({ loader = false, onDelete, onClose }) {
  return (
    <div className="alert-box" onClick={(e) => e.stopPropagation()}>
      <h2>Are you sure want to delete the links</h2>
      <p>
        Links selected will be deleted permanently and this action cannot be
        undone.
      </p>
      <div className="mt-40 fx gap-20">
        <button className="cancel-btn" onClick={onClose}>
          Cancel
        </button>
        <button className="delete-btn" onClick={onDelete}>
          {loader ? "Deleting..." : "Delete"}
        </button>
      </div>
    </div>
  );
}
