import environment from "../../../constants/Environment";
export function ProgressBar({ progress }) {
  return (
    <svg
      role="progressbar"
      width={105}
      height={105}
      viewBox="0 0 100 100"
      aria-valuemin={0}
      aria-valuemax={100}
      aria-valuenow={progress}
    >
      <circle
        cx="50%"
        cy="50%"
        r={42}
        shapeRendering="geometricPrecision"
        fill="none"
        stroke="#e6e6e6"
        strokeWidth={10}
      />
      <defs>
        <linearGradient id="linear-4">
          <stop offset="0%" stopColor=" #FF8F94" />
          <stop offset="100%" stopColor="#FB5F66" />
        </linearGradient>
      </defs>
      <circle
        cx="50%"
        cy="50%"
        r={42}
        shapeRendering="geometricPrecision"
        className="pie-circle-4"
        fill="none"
        strokeWidth={10}
        strokeDashoffset={progress}
        strokeDasharray={264}
        strokeLinecap="round"
        style={{
          transform: "rotate(-90deg)",
          transformOrigin: "50% 50%",
        }}
        stroke="url(#linear-4)"
        data-angel={75}
      />
      <text
        className="pie-text-4"
        x="50%"
        y="50%"
        fill="#000"
        textAnchor="middle"
        dy="0.35em"
        fontSize="16.2698px"
        fontWeight={500}
      >
        <tspan className="pie-percent-4">
          {Math.round(((264 - progress) / 264) * 100)}
        </tspan>
        <tspan className="pie-unit-4">{"%"}</tspan>
      </text>
    </svg>
  );
}

