import { useContext } from "react";
import { useMutation } from "@tanstack/react-query";
import { axiosPost } from "@/services/axiosService";
import processQuestions from "../utils/processQuestions";
import { BotContext } from "../store";

export default function useBotSave() {
  const [store, storeDispatch] = useContext(BotContext);

  return useMutation({
    mutationKey: ["bot", "saveBotInOne"],
    mutationFn: async (botData: Record<string, any>) => {
      const questionData = processQuestions(botData);

      const data = {
        ...botData,
        startNodeId: 1,
        questions: questionData,
      };

      const response = await axiosPost("/users/saveBot", data);
      const updatedBotData = response.data.data;

      storeDispatch({
        type: "set-botData",
        botData: {
          ...store.botData,
          lastPublishTime: updatedBotData.lastPublishTime,
        },
      });

      return updatedBotData;
    },
  });
}
