import React, { useState, useEffect } from 'react'


export function StarterQuestionsPopup({setCreateCatModal}){
    const [continueCreating, setContinueCreating] = useState<boolean>(false)
    useEffect(() => {
        const element = document.querySelector(".ai-bot-container") as HTMLDivElement;
        if (element) element.style.zIndex = "100";
        return () => {
          element.style.zIndex = "85";
        };
      }, []);

      const handleCreateCategory = () => {
        // setContinueCreating(true)
        // if(continueCreating) {
          // setIsCategory(true)
          setCreateCatModal(false)
        // }
      } 
      
  return (



<div className="createbot-modal">
    <div className="createbot-container" onClick={(e) => e.stopPropagation()}>
      <div className="starter-modal">
        <div className="wrkspace-img-cont">
        {!continueCreating ? <img src="https://dlvkyia8i4zmz.cloudfront.net/J3Eu496iQsS0hZ3nIK7g_Group_14568.svg" alt="" /> : <img src="https://dlvkyia8i4zmz.cloudfront.net/JXE9kbtCRhWYRXpHLkqi_Group_15367.svg" alt="" />}
        </div>
        <div className="starter-modal-body">
          <h1>
            {!continueCreating ? "Suggested Questions Limit Reached" : "Are you sure to continue?"}
          </h1>
          <p>
            {!continueCreating && "You can add upto 5 suggested questions in your bot"}
          </p>
          {/* <p>
            {!continueCreating ? "The maximum number of questions allowed per category has been attained. Add a new category for additional questions." : "Existing category will be deleted if you don't save it."}
          </p> */}
        </div>
        {/* <button className="create-cat-button" onClick={handleCreateCategory}>Create Category</button> */}
      </div>
      <div className="close flex-cnt" onClick={() => setCreateCatModal(false)}>
        <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path
            d="M7.09938 6.00985L11.7719 1.33715C12.076 1.03321 12.076 0.541783 11.7719 0.237843C11.468 -0.0660973 10.9766 -0.0660973 10.6726 0.237843L5.99993 4.91055L1.32737 0.237843C1.02329 -0.0660973 0.532001 -0.0660973 0.228062 0.237843C-0.0760205 0.541783 -0.0760205 1.03321 0.228062 1.33715L4.90062 6.00985L0.228062 10.6826C-0.0760205 10.9865 -0.0760205 11.4779 0.228062 11.7819C0.379533 11.9335 0.578696 12.0096 0.777716 12.0096C0.976736 12.0096 1.17576 11.9335 1.32737 11.7819L5.99993 7.10916L10.6726 11.7819C10.8242 11.9335 11.0233 12.0096 11.2223 12.0096C11.4213 12.0096 11.6203 11.9335 11.7719 11.7819C12.076 11.4779 12.076 10.9865 11.7719 10.6826L7.09938 6.00985Z"
            fill="#6D6D6D"
          />
        </svg>
      </div>
    </div>
  </div>
  )
}


