export const DeleteQuePopup = ({setDelModalOpen , selectedData ,quesAns,setQuesAns}) => {

  const  handleDel = () => {
    setQuesAns(quesAns.filter(item => item !== selectedData));
    setDelModalOpen(false)
  }

  useEffect(() => {
    const element = document.querySelector(".ai-bot-container") as HTMLDivElement;
    if (element) element.style.zIndex = "100";
    return () => {
      element.style.zIndex = "85";
    };
  }, []);
  return (
    <div className="createbot-modal">
    <div className="createbot-container" >
      <div className="success-cont">
        <div className="err-header">
        <svg width="132" height="132" viewBox="0 0 132 132" fill="none" xmlns="http://www.w3.org/2000/svg">
<circle cx="66" cy="66" r="66" fill="white" fillOpacity="0.2"/>
<circle cx="66.0002" cy="66.0002" r="57.8947" fill="white" fillOpacity="0.36"/>
<rect x="18.5254" y="18.5264" width="94.9474" height="94.9474" rx="47.4737" fill="white"/>
<rect x="42.002" y="42" width="48" height="48" rx="24" fill="#FB5F66"/>
<mask id="mask0_4007_4211" maskUnits="userSpaceOnUse" x="56" y="56" width="20" height="20">
<path fillRule="evenodd" clipRule="evenodd" d="M57.3623 59.7594L59.7623 57.3594L66.0023 63.5994L72.2423 57.3594L74.6423 59.7594L68.4023 65.9994L74.6423 72.2394L72.2423 74.6394L66.0023 68.3994L59.7623 74.6394L57.3623 72.2394L63.6023 65.9994L57.3623 59.7594Z" fill="white" stroke="white" strokeWidth="1.92" strokeLinecap="round" strokeLinejoin="round"/>
</mask>
<g mask="url(#mask0_4007_4211)">
<path d="M54.4814 54.4795H77.5214V77.5195H54.4814V54.4795Z" fill="white"/>
</g>
</svg>


        </div>
        <div className="succ-footer">
          <h1>Confirm deletion of Q/A</h1>
          <div className='err-ft-p'><p>
          This action is irreversible and will permanently remove the suggested message.
          </p></div>
          <div className="fx"><button className="leave-btn" onClick={() => setDelModalOpen(false)}>Cancel</button><button className="stay-btn" onClick={ handleDel }>Delete</button></div>
        </div>
      </div>
    </div>
  </div>
  )



}

