import { useEffect, useState, useReducer, useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import { nanoid } from "nanoid";
import { axiosGet, axiosPost } from "@/services/axiosService";
import { userSlice } from "@/redux/User";
import { BotIcon } from "@/assets/AiBotAssets";
import Preview from "@/pages/preview/Preview";
import AIBotTab_1 from "./components/AIBotTab_1";
import AIBotTab_2 from "./components/AIBotTab_2";
import AIBotTab_3 from "./components/AIBotTab_3";
import AIBotTab_4 from "./components/AIBotTab_4";
import ToastPopUp from "@/components/layoutElements/toasts/toast";
import ThreeDot from "@/components/loaders/ThreeDot";
import FetchUrlModal from "./components/FetchUrlModal";
import DeleteUrlModal from "./components/DeleteUrlModal";
import { State } from "@/redux/Types";
import useProgress from "@/utils/useProgress";
import { BotContext } from "./store";
import type { Url, FetchedFile } from "./types";
import ModalWrapper from "./components/ModalWrapper";
import SmallModal from "@/components/layoutElements/modals/SmallModal";
import setValueByPath from "@/utils/setValueByPath";
import { useQueryClient } from "@tanstack/react-query";
import { processUrl } from "@/utils/processUrl";

interface Props {
  onSave: (data: Record<string, any>) => void;
}

export default function Builder({ onSave }: Props) {
  const dispatch = useDispatch();
  const getProgress = useProgress();
  const queryClient = useQueryClient();
  const [store, storeDispatch] = useContext(BotContext);
  const plan: any = useSelector((state: State) => state.user.plan);
  const trainingBotId = useSelector((state: State) => state.user.trainingBotId);
  const trainingPending = useSelector(
    (state: State) => state.user.trainingPending,
  );
  const [uploadProgress, setUploadProgress] = useState<number>(264);
  const [files, setFiles] = useState<File[]>([]);
  const [fetchedFiles, setFetchedFiles] = useState<FetchedFile[]>([]);
  const [previewKey, reloadPreview] = useReducer((state) => !state, false);
  const [saveLoader, setSaveLoader] = useState<boolean>(false);
  const [showProgress, setShowProgress] = useState<boolean>(false);
  const [shadow, setShadow] = useState<boolean>(false);
  const [bottomShadow, setBottomShadow] = useState<boolean>(false);
  const [fetchModal, setFetchModal] = useState<boolean>(false);
  const [deleteModal, setDeleteModal] = useState<boolean>(false);
  const [characterLimit, setCharacterLimit] = useState<number>(0);
  const [websiteUrl, setWebsiteUrl] = useState<string>("");
  const [fileUrl, setFileUrl] = useState<string>("");
  const [fetchLoader, setFetchLoader] = useState<boolean>(false);
  const [refreshLoader, setRefreshLoader] = useState<boolean>(false);
  const [fetchMoreLoader, setFetchMoreLoader] = useState<boolean>(false);
  const [fetchProgress, setFetchProgress] = useState<number>(0);
  const [fileLoader, setFileLoader] = useState<boolean>(false);
  const [extraUrl, setExtraUrl] = useState<string>("");
  const [extraLoader, setExtraLoader] = useState<boolean>(false);
  const [fetchRemains, setFetchRemains] = useState<boolean>(false);
  const [allTrained, setAllTrained] = useState<boolean>(false);
  const [showModal, setShowModal] = useState<boolean>(false);
  const characterUsed = store.botData.aiBotConfig?.characterUsed ?? 0;

  useEffect(() => {
    getCompany();
  }, []);

  function refetchBotData() {
    queryClient.invalidateQueries({ queryKey: ["bot", "completeBot"] });
  }

  async function getCompany() {
    try {
      const response = await axiosGet("/users/getSingleCompany");
      const { company } = response.data.data;
      setCharacterLimit(company?.ai_currentLimits?.characters ?? 0);
    } catch (err) {
      console.log(err);
    }
  }

  function handleDataChange(field: string, value: any, _: any) {
    if (
      store.botData?.chatTranscript &&
      field === "aiBotConfig.collectUserData" &&
      !value
    ) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            "If you disable the email field we will not be able to send the chat transcript.",
        }),
      );
      return;
    }

    const botData = setValueByPath(store.botData, field, value);
    onSave(botData);
  }

  async function handleSave() {
    setSaveLoader(true);

    onSave(store.botData);
    await new Promise((resolve) => setTimeout(resolve, 1000));
    reloadPreview();
    dispatch(
      userSlice.actions.setToastPopUp({
        show: true,
        toastType: "success",
        message: "Data Saved",
      }),
    );
    setSaveLoader(false);
  }

  async function handleTrain() {
    if (!store.crawlData.size && !files.length && !fetchedFiles.length) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            "Training data source is missing. Please specify a data source to train the bot",
        }),
      );
      return;
    }

    const isFileTraining = files.length || fetchedFiles.length;
    const isWebpageTraining = store.crawlData.size;

    const urls: string[] = [];
    store.crawlData.forEach((value) => {
      if (value.selected) urls.push(value.url);
    });
    store.extraData.forEach((value) => {
      if (value.selected) urls.push(value.url);
    });

    if (isFileTraining && isWebpageTraining) {
      if (urls.length) {
        await trainDocument(true);
        await trainWebpage(urls, true);
      } else {
        await trainDocument(false);
      }
    } else if (isFileTraining) {
      await trainDocument(false);
    } else if (isWebpageTraining) {
      await trainWebpage(urls, false);
    }
  }

  async function trainDocument(halfProgress: boolean) {
    let interval: NodeJS.Timeout | undefined;
    try {
      if (characterUsed >= characterLimit)
        throw new Error(
          "Your characters limit exceeded. Please upgrade your plan",
        );

      const formData = new FormData();
      let totalTime = 0;
      for (const file of files) {
        formData.append("file", file, file.name);
        totalTime += Math.round((10 * 1000 * file.size) / (1000 * 1024)); // estimate: 1000kb - 10 sec
      }

      for (const file of fetchedFiles) {
        formData.append("fileUrls[]", file.url);
        totalTime += Math.round((10 * 1000 * file.size) / (10 * 1024)); // estimate: 10kb - 10sec
      }
      formData.append("botCustomId", store.botData.botCustomId as string);
      formData.append("botCreatedAt", store.botData.createdAt as string);

      const tickTime = 1000;
      const totalTick = Math.ceil(totalTime / tickTime);

      let progress = 0;
      interval = setInterval(
        (function cb() {
          dispatch(
            userSlice.actions.setTrainData({
              pending: true,
              progress: halfProgress ? progress / 2 : progress,
              botId: store.botData.botCustomId,
              type: "document",
            }),
          );
          progress = Math.min(progress + 100 / totalTick, 99);

          return cb;
        })(),
        tickTime,
      );

      const response = await axiosPost("/ai/train-document", formData);
      const { message } = response.data;

      setFiles([]);
      setFetchedFiles([]);
      if (halfProgress) return;

      refetchBotData();
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message,
        }),
      );
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: err.response?.data?.message ?? err?.message,
        }),
      );
    } finally {
      if (interval) clearInterval(interval);
      dispatch(
        userSlice.actions.setTrainData({
          pending: halfProgress,
          process: halfProgress ? 50 : 100,
          botId: store.botData.botCustomId,
        }),
      );
    }
  }

  async function trainWebpage(urls: string[], halfProgress: boolean) {
    try {
      if (!urls.length) throw new Error("No link selected to train");
      if (characterUsed >= characterLimit)
        throw new Error(
          "Your characters limit exceeded. Please upgrade your plan",
        );

      dispatch(
        userSlice.actions.setTrainData({
          pending: true,
          progress: halfProgress ? 50 : 0,
          botId: store.botData.botCustomId,
          type: "webpage",
        }),
      );

      await axiosPost("/ai/train-webpage", {
        urls,
        botCustomId: store.botData.botCustomId,
        botCreatedAt: store.botData.createdAt,
      });

      getProgress({
        halfProgress,
        async onFinish() {
          storeDispatch({ type: "crawl-reset" });
          refetchBotData();
        },
      });
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: err.response?.data?.message ?? err?.message,
        }),
      );
      dispatch(
        userSlice.actions.setTrainData({
          pending: false,
          process: 100,
          botId: store.botData.botCustomId,
        }),
      );
    }
  }

  function handleFileUpload(event: any) {
    const fileData = Array.from<File>(event.target.files);
    if (!fileData.length) return;
    const extractedFile = fileData.filter(
      (obj) =>
        !files.some(
          (file) => JSON.stringify(file.name) === JSON.stringify(obj.name),
        ),
    );
    if (!extractedFile.length) {
      return dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            "The file you are trying to upload has already been used to train the bot. Please upload a different file",
        }),
      );
    }
    if (extractedFile.length > 50) {
      return dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            "Maximum files count exceeded. Please below upload 50 number of files",
        }),
      );
    }
    let validatedFiles = extractedFile.filter(
      (file) =>
        file.name.toLowerCase().slice(-3) === "pdf" ||
        file.name.toLowerCase().slice(-4) === "docx" ||
        file.name.toLowerCase().slice(-3) === "txt",
    );
    if (validatedFiles.length !== extractedFile.length) {
      return dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            "File type that you are trying to upload is not supported by our system. Please try uploading a PDF, TXT or .DOC file",
        }),
      );
    }

    const validatedFileSize = extractedFile.filter(
      (file) => file.size <= plan?.ai_currentLimits?.fileSize,
    );
    if (validatedFileSize.length !== extractedFile.length) {
      return dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            "The file size exceeds the limit supported in your current plan",
        }),
      );
    }

    let progressRemain = 264;
    setShowProgress(true);
    setUploadProgress(progressRemain);
    const timerInterval = setInterval(() => {
      setUploadProgress((progressRemain -= 8));
      if (progressRemain < 0) {
        clearInterval(timerInterval);
        setShowProgress(false);
        setFiles([...files, ...extractedFile]);
      }
    }, 22);
  }

  function handleFileRemove(index: number) {
    const temp = [...files];
    temp.splice(index, 1);
    setFiles(temp);
  }

  function fetchedFileRemove(index: number) {
    const temp = [...fetchedFiles];
    temp.splice(index, 1);
    setFetchedFiles(temp);
  }

  async function fetchUrl() {
    let interval: NodeJS.Timeout | undefined;
    try {
      if (
        !websiteUrl ||
        fetchLoader ||
        refreshLoader ||
        fetchMoreLoader ||
        extraLoader
      )
        return;

      const url = processUrl(websiteUrl);
      await axiosPost("/ai/check-webpage", { url });

      setFetchLoader(true);
      setFetchProgress(0);
      interval = setInterval(() => {
        setFetchProgress((fetchProgress) => {
          let increment = 0;
          if (fetchProgress < 40) increment = 10;
          else if (fetchProgress < 70) increment = 5;
          else if (fetchProgress < 90) increment = 3;
          else increment = 1;
          return Math.min(fetchProgress + increment, 99);
        });
      }, 3000);

      const mode = url.href.endsWith(".xml") ? "sitemap" : "crawl";
      const crawlData = await _handleFetch(url, mode);

      storeDispatch({ type: "crawl-init", url: url.href, crawlData });
      setFetchLoader(false);
      setFetchModal(true);
      setWebsiteUrl("");
      clearInterval(interval);
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Something went wrong",
        }),
      );
      setFetchLoader(false);
      if (interval) clearInterval(interval);
    }
  }

  async function refreshUrl() {
    try {
      if (fetchLoader || refreshLoader || fetchMoreLoader || extraLoader)
        return;
      setRefreshLoader(true);

      const crawlData = await _handleFetch(
        new URL(store.url),
        "crawl",
        [],
        true,
      );

      storeDispatch({ type: "crawl-init", crawlData });
      setRefreshLoader(false);
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Something went wrong",
        }),
      );
      setRefreshLoader(false);
    }
  }

  async function fetchMoreUrl() {
    try {
      if (fetchLoader || refreshLoader || fetchMoreLoader || extraLoader)
        return;
      setFetchMoreLoader(true);

      const crawledUrls: string[] = [];
      store.crawlData.forEach((value) => {
        crawledUrls.push(value.url);
      });

      const crawlData = await _handleFetch(
        new URL(store.url),
        "crawl",
        crawledUrls,
      );

      storeDispatch({ type: "crawl-add", crawlData });
      setFetchMoreLoader(false);
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Something went wrong",
        }),
      );
      setFetchMoreLoader(false);
    }
  }

  async function fetchExtraUrl(selectedCharacterCount: number) {
    try {
      if (
        !extraUrl ||
        fetchLoader ||
        refreshLoader ||
        fetchMoreLoader ||
        extraLoader
      )
        return;
      setExtraLoader(true);

      const url = processUrl(extraUrl);
      if (
        store.botData.aiSource.urls.some((data: any) => data.url === url.href)
      ) {
        throw new Error("Link already trained");
      }

      let alreadyFetch = false;
      let newFetch = true;
      let extraId = "";

      store.crawlData.forEach((value) => {
        if (value.url === url.href) alreadyFetch = true;
      });
      store.extraData.forEach((value, key) => {
        if (value.selected && value.url === url.href) alreadyFetch = true;
        if (!value.selected) {
          newFetch = false;
          extraId = key;
        }
      });

      if (alreadyFetch) {
        throw new Error("Link already fetched");
      }

      const characterRemaining = characterLimit - characterUsed;

      if (newFetch) {
        const extraData = await _handleFetch(url, "extra");

        let characterCount = 0;
        extraData.forEach((value) => {
          characterCount += value.characterCount;
        });

        if (selectedCharacterCount + characterCount > characterRemaining) {
          storeDispatch({ type: "extra-add", extraData, selected: false });
          throw new Error(
            "Max character limit reached. Remove other links or upgrade your plan.",
          );
        }

        storeDispatch({ type: "extra-add", extraData, selected: true });
      } else {
        const characterCount =
          store.extraData.get(extraId)?.characterCount ?? 0;

        if (selectedCharacterCount + characterCount > characterRemaining) {
          throw new Error(
            "Max character limit reached. Remove other links or upgrade your plan.",
          );
        }
        storeDispatch({ type: "extra-select", id: extraId });
      }

      setExtraLoader(false);
      setExtraUrl("");
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Something went wrong",
        }),
      );
      setExtraLoader(false);
    }
  }

  async function _handleFetch(
    url: URL,
    mode: "crawl" | "sitemap" | "extra",
    crawledUrls: string[] = [],
    refresh: boolean = false,
  ) {
    const trainedUrls: string[] = [];

    for (const data of store.botData.aiSource.urls) {
      if (url.origin === new URL(data.url ?? data.link).origin) {
        trainedUrls.push(data.url);
      }
    }

    const res = await axiosPost("/ai/crawl", {
      url: url.href,
      mode,
      crawledUrls,
      trainedUrls,
      botCustomId: store.botData.botCustomId,
      refresh,
    });

    const { crawlData, fetchRemains, stats, characterUsed, characterLimit } =
      res.data.data;

    const result: Map<string, Url> = new Map();
    for (const data of crawlData) {
      const id = nanoid();
      result.set(id, {
        ...data,
        selected: false,
      });
    }

    setCharacterLimit(characterLimit);
    // setCharacterUsed(characterUsed);
    setAllTrained(!stats.new && stats.old);
    if (mode !== "extra") setFetchRemains(fetchRemains);

    return result;
  }

  async function fetchFile() {
    try {
      if (!fileUrl || fileLoader) return;
      if (fetchedFiles.some((file) => file.url === fileUrl)) {
        throw new Error("Already fetched");
      }
      setFileLoader(true);

      const response = await axiosPost("/ai/fetch-file", { url: fileUrl });
      const file = response.data.data.file;

      if (!ALLOWED_FILE_FORMAT.includes(file.type)) {
        throw new Error(
          "File type that you are trying to upload is not supported by our system. Please try uploading a PDF, TXT or .DOC file",
        );
      }

      if (file.size > (plan.ai_currentLimits?.fileSize ?? 0)) {
        throw new Error(
          "The file size exceeds the limit supported in your current plan",
        );
      }

      setFetchedFiles(
        fetchedFiles.concat({
          url: fileUrl,
          ...file,
        }),
      );

      setFileLoader(false);
      setFileUrl("");
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Something went wrong",
        }),
      );
      setFileLoader(false);
    }
  }

  function handleScroll(event: any) {
    if (event.currentTarget.scrollTop > 100) setShadow(true);
    else setShadow(false);
    const box = event.currentTarget;
    const scrollableHeight = box.scrollHeight - box.clientHeight;
    if (box.scrollTop >= scrollableHeight - 0.5) {
      setBottomShadow(false);
    } else setBottomShadow(true);
  }

  return (
    <>
      {showModal ? <SmallModal setShowModal={setShowModal} /> : <></>}
      <div className="ai-bot-container fx jstfy-cnt">
    
        <p>Data Source</p>
        <div
          className="ai-bot-wrapper fx jstfy-sp-btw gap-30 wd-100"
          tabIndex={-1}
        >
          <div
            className="cbb-builder-container  fx fx-d-col"
            onScroll={handleScroll}
          >
            <div className="ai-bot-setup fx fx-d-col">
              <div className="aibot-header fx al-cnt mb-20">
                <div className="header-logo flex-cnt">
                  <BotIcon />
                </div>
                <div>
                  <h1>AI Bot </h1>
                </div>
              </div>
              <div className={`sticky-tabs ${shadow && "tabs-shadow"}`}>
                <div className="ai-switch-btns flex-cnt gap-10">
                  <button
                    className={`flex-cnt botSetup gap-8 ${
                      store.tab === 1 ? "aibot-active" : ""
                    }`}
                    onClick={() => storeDispatch({ type: "tab-set", tab: 1 })}
                  >
                    <BotSetupIcon /> BOT SETUP
                  </button>
                  <button
                    className={`flex-cnt dataSource gap-8 ${
                      store.tab === 2 ? "aibot-active" : ""
                    }`}
                    onClick={() => storeDispatch({ type: "tab-set", tab: 2 })}
                  >
                    <DataSourceIcon /> DATA SOURCE
                  </button>
                  <button
                    className={`flex-cnt startQues gap-8 ${
                      store.tab === 3 ? "aibot-active" : ""
                    }`}
                    onClick={() => storeDispatch({ type: "tab-set", tab: 3 })}
                  >
                    <StartQuesIcon /> SUGGESTED QUESTIONS
                  </button>
                  <button
                    className={`flex-cnt botDesign gap-8 ${
                      store.tab === 4 ? "aibot-active" : ""
                    }`}
                    onClick={() => storeDispatch({ type: "tab-set", tab: 4 })}
                  >
                    <BotDesignIcon /> BOT DESIGN
                  </button>
                </div>
              </div>
              {store.tab === 1 && (
                <AIBotTab_1
                  botData={store.botData}
                  onDataChange={handleDataChange}
                  plan={plan}
                />
              )}
              {store.tab === 2 && (
                <AIBotTab_2
                  botData={store.botData}
                  files={files}
                  fetchedFiles={fetchedFiles}
                  onFileUpload={handleFileUpload}
                  onFileRemove={handleFileRemove}
                  onFetchedFileRemove={fetchedFileRemove}
                  fetchUrl={fetchUrl}
                  showProgress={showProgress}
                  uploadProgress={uploadProgress}
                  parentForceReload={refetchBotData}
                  setFetchModal={setFetchModal}
                  setDeleteModal={setDeleteModal}
                  planDetails={plan}
                  websiteUrl={websiteUrl}
                  setWebsiteUrl={setWebsiteUrl}
                  fileUrl={fileUrl}
                  setFileUrl={setFileUrl}
                  fetchLoader={fetchLoader}
                  fileLoader={fileLoader}
                  fetchFile={fetchFile}
                />
              )}
              {store.tab === 3 && (
                <AIBotTab_3
                  aiStarterQues={store.botData?.aiStarterQues}
                  onDataChange={handleDataChange}
                />
              )}
              {store.tab === 4 && (
                <AIBotTab_4
                  chatConfig={store.botData?.chatConfig}
                  onDataChange={handleDataChange}
                />
              )}
              <div
                className={`flex-cnt n0-mt mb-20 ${
                  store.tab === 2 ? "train_btn" : ""
                } ${store.tab === 2 && bottomShadow ? "train_shadow" : ""}`}
              >
                {store.tab === 2 ? (
                  <div className="save-btn" onClick={handleTrain}>
                    {store.botData.botCustomId === trainingBotId &&
                    trainingPending ? (
                      <div className="fx al-fx-end jstfy-cnt gap-6">
                        Training <ThreeDot />
                      </div>
                    ) : (
                      "Train Bot"
                    )}
                  </div>
                ) : (
                  <div className="save-btn" onClick={handleSave}>
                    {saveLoader ? (
                      <div className="fx al-fx-end jstfy-cnt gap-6">
                        Saving <ThreeDot />
                      </div>
                    ) : (
                      "Save"
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
          <Preview
            key={previewKey}
            embed={true}
            chatConfig={store.botData.chatConfig}
            environment={"dev"}
          />
          {fetchModal && (
            <ModalWrapper>
              <FetchUrlModal
                characterLimit={characterLimit}
                characterUsed={characterUsed}
                fetchMoreUrl={fetchMoreUrl}
                fetchExtraUrl={fetchExtraUrl}
                refreshUrl={refreshUrl}
                extraUrl={extraUrl}
                setExtraUrl={setExtraUrl}
                refreshLoader={refreshLoader}
                fetchMoreLoader={fetchMoreLoader}
                extraLoader={extraLoader}
                fetchRemains={fetchRemains}
                allTrained={allTrained}
                crawlLimit={plan?.ai_currentLimits?.crawlLimit ?? 3000}
                onClose={() => setFetchModal(false)}
                fetchModal={fetchModal}
              />
            </ModalWrapper>
          )}
          {deleteModal && (
            <ModalWrapper>
              <DeleteUrlModal
                trainedUrls={store.botData.aiSource.urls}
                botCustomId={store.botData.botCustomId}
                botCreatedAt={store.botData.createdAt}
                characterLimit={characterLimit}
                characterUsed={characterUsed}
                onClose={() => setDeleteModal(false)}
                onReload={refetchBotData}
                deleteModal={deleteModal}
              />
            </ModalWrapper>
          )}
          {fetchLoader && (
            <ToastPopUp
              setToast={true}
              toastType={`progress`}
              progressStatus={fetchProgress}
              text={
                "This may take a few seconds to minutes depending on the number of links"
              }
            />
          )}
        </div>
      </div>
    </>
  );
}

const ALLOWED_FILE_FORMAT = [
  "text/plain",
  "application/pdf",
  "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
  "application/vnd.openxmlformats-officedocument.wordprocessingml.template",
];

