import { useContext, useEffect, useMemo } from "react";
import {
  EditLinkIcon,
  NoFilesIcon,
  PdfIcon,
  TextFileIcon,
  UploadIcon,
  ProgressBar,
  CircleBg,
  EditSvg,
} from "@/assets/AiBotAssets";
import { axiosPost } from "@/services/axiosService";
import { userSlice } from "@/redux/User";
import { useDispatch } from "react-redux";
import ThreeDot from "@/components/loaders/ThreeDot";
import { DeleteIcon } from "@/assets/IntegrationIcons";
import { BotContext } from "../store";
import type { Url, FetchedFile } from "../types";

function AIBotTab_2({
  botData,
  files,
  fetchedFiles,
  onFileUpload,
  onFileRemove,
  onFetchedFileRemove,
  fetchUrl,
  showProgress,
  uploadProgress,
  parentForceReload,
  planDetails,
  websiteUrl,
  setWebsiteUrl,
  fileUrl,
  setFileUrl,
  fetchLoader,
  fileLoader,
  setFetchModal,
  setDeleteModal,
  fetchFile,
}) {
  const dispatch = useDispatch();
  const [store, storeDispatch] = useContext(BotContext);
  const selectedData = useMemo(() => {
    const selectedData: Url[] = [];
    store.crawlData.forEach((value) => {
      if (value.selected) selectedData.push(value);
    });

    return selectedData;
  }, [store.crawlData]);

  async function deleteTrainDocument(source: string) {
    try {
      if (!source) throw new Error("Invalid source");

      const res = await axiosPost("/ai/delete-content", {
        sources: [source],
        botCustomId: botData.botCustomId,
        botCreatedAt: botData.createdAt,
        sourceType: "documents",
      });

      parentForceReload();
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message:
            "Selected data has been removed from the training data. Retrain your bot to update it.",
        }),
      );
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: err.message,
        }),
      );
    }
  }

  const handleZIndex = () => {
    const mainMenuElement = document.querySelector(
      ".main-menu-ai-builder",
    ) as HTMLElement;
    const shortLeftColElement = document.querySelector(
      ".short-left-col",
    ) as HTMLElement;
    if (mainMenuElement) {
      mainMenuElement.style.zIndex = "0";
    }
    if (shortLeftColElement) {
      shortLeftColElement.style.zIndex = "0";
    }
  };

  return (
    <div className="choose-source-container m-l-r-b-20 animate-up-d mb-0">
      <div>
        <input type="radio" name="data-source" />
        <h3>Data Source Configuration</h3>
        <div className="url-crawl ai-info-wrapper no-mt source-config">
          <div className="ai-input-wrap  config_input-wrapper  config-or-line">
            <label>Add website or sitemap link here</label>
            <div className="fx website__link gap-10">
              <input
                type="text"
                placeholder="https://chatbotbuilder.net"
                value={websiteUrl}
                onChange={(event) => setWebsiteUrl(event.target.value)}
                onKeyDown={(event) => event.key === "Enter" && fetchUrl()}
              />
              <button
                className={
                  !websiteUrl?.length || fileLoader
                    ? "fetch-btn fetch-disable"
                    : "fetch-btn"
                }
                onClick={fetchUrl}
              >
                {fetchLoader ? (
                  <div className="fx al-fx-end jstfy-cnt fetching-link gap-6">
                    Fetching <ThreeDot />
                  </div>
                ) : (
                  "Fetch Links"
                )}
              </button>
            </div>
          </div>
          {/* <div className="ai-input-wrap  config_input-wrapper">
            <label>Google Sheet Link</label>
            <div className="fx gap-10">
              <input
                type="text"
                placeholder="Paste link here"
                // value={url} onChange={(e) => onUrlChange(e.target.value.trim())}
              />
              <button
                className={`fetch-btn ${!url?.length ? "fetch-disable" : ""}`}
                onClick={onFetchLink}
                disabled={!url?.length && loader}
              >
                {fetchLoader ? (selected
                  <div className="fx al-fx-end jstfy-cnt gap-6 fetching-link">
                    Fetching <ThreeDot />
                  </div>
                ) : (
                  "Get File" 
                )}
              </button>
            </div>
          </div> */}
        </div>
        <div className="fx jstfy-sp-btw al-cnt mt-20">
          <div className="links-include fx al-cnt gap-10">
            {store.crawlData.size || store.extraData.size ? (
              <>
                <label>Links to include for Bot training</label>
                <div className="link_count">{selectedData?.length}</div>
              </>
            ) : (
              <>
                <label>Trained Links</label>
                <div className="link_count">
                  {botData?.aiSource?.urls?.length}
                </div>
              </>
            )}
          </div>
          {store.crawlData.size || store.extraData.size ? (
            <button
              className="edit-link-btn flex-cnt gap-5"
              onClick={() => {
                setFetchModal(true);
                handleZIndex();
              }}
            >
              <EditSvg /> Edit
            </button>
          ) : botData?.aiSource?.urls?.length ? (
            <button
              className="edit-link-btn remove flex-cnt gap-5"
              onClick={() => {
                setDeleteModal(true);
                handleZIndex();
              }}
            >
              <DeleteIcon /> Remove Link
            </button>
          ) : null}
        </div>
        <div className="url-crawl ai-info-wrapper mt-10">
          <div className="ai-input-wrap">
            <div className="">
              {store.crawlData.size || store.extraData.size ? (
                selectedData.reduce((pre, data) => pre + data.url + "\n", "")
                  .length ? (
                  <textarea
                    className="edit-links edit-textarea"
                    placeholder=""
                    value={selectedData.reduce(
                      (pre, data) => pre + data.url + "\n",
                      "",
                    )}
                    onClick={() => {
                      selectedData?.length && setFetchModal(true);
                      handleZIndex();
                    }}
                  />
                ) : (
                  <div className="no-links p-relative">
                    <CircleBg />
                    <p>No links selected</p>
                  </div>
                )
              ) : botData?.aiSource?.urls?.length ? (
                <textarea
                  className="edit-links edit-textarea"
                  placeholder=""
                  value={botData.aiSource.urls.reduce(
                    (pre: string, data: any) =>
                      pre + (data.url ?? data.link) + "\n",
                    "",
                  )}
                  onClick={() => {
                    botData.aiSource.urls?.length && setDeleteModal(true);
                    handleZIndex();
                  }}
                />
              ) : (
                <div className="no-links p-relative">
                  <CircleBg />
                  <p>No links fetched</p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <div className="uploadFile mt-20">
        <input type="radio" name="data-source" />
        <h3>UPLOAD DATA FOR BOT TRAINING</h3>
        <div className="url-crawl  ai-info-wrapper flex-cnt fx-d-col g-58">
          <div className="ai-input-wrap  config_input-wrapper  config-or-line wd-100 responsive__hide">
            <label className="file-link-info">
              File Link
              {/* <div className="data-source-tooltip">
                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13" fill="none">
                  <g clipPath="url(#clip0_3695_64581)">
                    <path d="M6.5 0C5.21442 0 3.95772 0.381218 2.8888 1.09545C1.81988 1.80968 0.986755 2.82484 0.494786 4.01256C0.00281636 5.20028 -0.125905 6.50721 0.124899 7.76809C0.375703 9.02896 0.994767 10.1872 1.90381 11.0962C2.81285 12.0052 3.97104 12.6243 5.23192 12.8751C6.49279 13.1259 7.79973 12.9972 8.98744 12.5052C10.1752 12.0132 11.1903 11.1801 11.9046 10.1112C12.6188 9.04229 13 7.78558 13 6.5C12.9981 4.77667 12.3127 3.12445 11.0941 1.90586C9.87556 0.687282 8.22334 0.00186392 6.5 0V0ZM6.5 11.375C5.53582 11.375 4.59329 11.0891 3.7916 10.5534C2.98991 10.0177 2.36507 9.25637 1.99609 8.36558C1.62711 7.47479 1.53057 6.49459 1.71867 5.54893C1.90678 4.60328 2.37108 3.73464 3.05286 3.05285C3.73464 2.37107 4.60328 1.90677 5.54894 1.71867C6.49459 1.53057 7.47479 1.62711 8.36558 1.99609C9.25637 2.36506 10.0177 2.98991 10.5534 3.7916C11.0891 4.59329 11.375 5.53582 11.375 6.5C11.3734 7.79245 10.8593 9.03151 9.94541 9.94541C9.03151 10.8593 7.79245 11.3734 6.5 11.375Z" fill="black" fillOpacity="0.5"/>
                    <path d="M6.25366 5.17213H6.09116C5.87828 5.1669 5.67111 5.24138 5.51029 5.38097C5.34946 5.52055 5.24657 5.71517 5.22179 5.92667C5.20762 6.13826 5.27673 6.34701 5.41437 6.50833C5.55201 6.66965 5.74727 6.77077 5.95845 6.79009V9.30884C5.95845 9.52433 6.04406 9.73099 6.19643 9.88337C6.3488 10.0357 6.55547 10.1213 6.77095 10.1213C6.98644 10.1213 7.19311 10.0357 7.34548 9.88337C7.49785 9.73099 7.58345 9.52433 7.58345 9.30884V6.50192C7.58345 6.14924 7.44335 5.811 7.19397 5.56162C6.94458 5.31224 6.60635 5.17213 6.25366 5.17213Z" fill="black" fillOpacity="0.5"/>
                    <path d="M6.40802 4.58581C6.59185 4.58581 6.77156 4.53129 6.92442 4.42916C7.07727 4.32702 7.19641 4.18186 7.26676 4.01201C7.33711 3.84217 7.35552 3.65528 7.31966 3.47497C7.28379 3.29467 7.19526 3.12904 7.06527 2.99905C6.93528 2.86906 6.76966 2.78053 6.58935 2.74467C6.40905 2.7088 6.22216 2.72721 6.05231 2.79756C5.88247 2.86791 5.7373 2.98705 5.63516 3.1399C5.53303 3.29276 5.47852 3.47247 5.47852 3.65631C5.47844 3.77839 5.50244 3.89929 5.54912 4.0121C5.59581 4.1249 5.66428 4.2274 5.7506 4.31372C5.83693 4.40005 5.93942 4.46851 6.05223 4.5152C6.16503 4.56188 6.28593 4.58588 6.40802 4.58581Z" fill="black" fillOpacity="0.5"/>
                  </g>
                  <defs>
                    <clipPath id="clip0_3695_64581">
                      <rect width="13" height="13" fill="white"/>
                    </clipPath>
                  </defs>
                </svg>
                <span>In this section you can add your Googlesheet link containing the list of url's that you would like to use to train your bot</span>
              </div> */}
            </label>
            <div className="fx gap-10">
              <input
                type="text"
                placeholder="Paste link here"
                value={fileUrl}
                onChange={(event) => setFileUrl(event.target.value)}
                onKeyDown={(event) => event.key === "Enter" && fetchFile()}
              />
              <button
                className={`${
                  !fileUrl?.length || fetchLoader
                    ? "fetch-btn fetch-disable"
                    : "fetch-btn"
                }`}
                onClick={fetchFile}
              >
                {fileLoader ? (
                  <div className="fx al-fx-end jstfy-cnt fetching-link gap-6">
                    Fetching <ThreeDot />
                  </div>
                ) : (
                  "Get File"
                )}
              </button>
            </div>
          </div>
          <div className="file-uploads-wrapper wd-100">
            <p className="responsive__hide">File Uploads</p>
            <div className="file-uploads-inner fx gap-20">
              <input
                type="file"
                name="file-input"
                id="file-input"
                className="file-input__input"
                accept=".pdf, .txt, .docx"
                multiple={true}
                value=""
                onChange={(event) => {
                  onFileUpload(event);
                }}
              />
              <label htmlFor="file-input" className="upload-box-wrapper wd-50">
                <div className="upload-box flex-cnt fx-d-col w-auto ">
                  {!showProgress && (
                    <div className="upload-files flex-cnt fx-d-col">
                      <div className="upload-icon-frame">
                        <img
                          src="https://dlvkyia8i4zmz.cloudfront.net/LCPtGMwTQaaHG5Bf2RSD_Upload_icon.svg"
                          alt="Upload Icon"
                        />
                      </div>
                      <span className="browse-btn mt-10"> Upload File</span>
                      <p className="file-size">Max File Size: 10 MB</p>
                      <div className="uploadDetails fx">
                        <p>Supported formats: PDF, TXT</p>
                        {/* <hr /> */}
                        {/* <p>File Size: 5MB (max 10 files)</p> */}
                        {/* <p>File Size: {formatBytes(planDetails?.ai_currentLimits?.fileSize)}</p> */}
                      </div>
                    </div>
                  )}
                  {showProgress && (
                    <div className="progress-bar flex-cnt fx-d-col">
                      <ProgressBar progress={uploadProgress} />
                      {/* <h1 className="fx al-cnt">
                    {files[files.length - 1]?.name}
                  </h1> */}
                    </div>
                  )}
                </div>
              </label>
              {files?.length ||
              fetchedFiles?.length ||
              botData.aiSource?.documents?.length ? (
                <div className="uploadedFilegrid wd-50">
                  <div className="uploadedFile-container">
                    {botData?.aiSource?.documents?.map((file, index) => (
                      <div className="file-typ-bx fx al-cnt gap-15" key={index}>
                        <div className="file-typ-img">
                          {file.mime === "application/pdf" && <PdfIcon />}
                          {file.mime === "text/plain" && <TextFileIcon />}
                          {file.mime ===
                            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" && (
                            <TextFileIcon />
                          )}
                          {file.mime ===
                            "application/vnd.openxmlformats-officedocument.wordprocessingml.template" && (
                            <TextFileIcon />
                          )}
                        </div>
                        <div className="file-typ-name">
                          <h1 className="fx al-cnt">{file.name}</h1>
                          <button
                            className="fx al-cnt"
                            onClick={() => deleteTrainDocument(file.name)}
                          >
                            remove
                          </button>
                        </div>
                      </div>
                    ))}
                    {files?.map((file: File, index) => (
                      <div
                        className="file-typ-bx fx al-cnt file-type gap-10"
                        key={index}
                      >
                        <div className="file-typ-img">
                          {file.type === "application/pdf" && <PdfIcon />}
                          {file.type === "text/plain" && <TextFileIcon />}
                          {file.type ===
                            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" && (
                            <TextFileIcon />
                          )}
                          {file.type ===
                            "application/vnd.openxmlformats-officedocument.wordprocessingml.template" && (
                            <TextFileIcon />
                          )}
                        </div>
                        <div className="file-typ-name">
                          <h1 className="fx al-cnt">{file.name}</h1>
                          <div className="file-typ-btns">
                            {/* <button className="fx al-cnt preview">Preview</button> */}
                            <button
                              className="fx al-cnt"
                              onClick={() => onFileRemove(index)}
                            >
                              Remove
                            </button>
                          </div>
                        </div>
                      </div>
                    ))}
                    {fetchedFiles.map((file: FetchedFile, index: number) => (
                      <div
                        className="file-typ-bx fx al-cnt file-type gap-10"
                        key={index}
                      >
                        <div className="file-typ-img">
                          {file.type === "application/pdf" && <PdfIcon />}
                          {file.type === "text/plain" && <TextFileIcon />}
                          {file.type ===
                            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" && (
                            <TextFileIcon />
                          )}
                          {file.type ===
                            "application/vnd.openxmlformats-officedocument.wordprocessingml.template" && (
                            <TextFileIcon />
                          )}
                        </div>
                        <div className="file-typ-name">
                          <h1 className="fx al-cnt">{file.name}</h1>
                          <div className="file-typ-btns">
                            {/* <button className="fx al-cnt preview">Preview</button> */}
                            <button
                              className="fx al-cnt"
                              onClick={() => onFetchedFileRemove(index)}
                            >
                              Remove
                            </button>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                  <div
                    className={`fadeout-div ${
                      files?.length > 4 ? "d-block" : ""
                    }`}
                  ></div>
                </div>
              ) : (
                <div className="uploadedFile wd-50">
                  <div className="url-crawl ai-info-wrapper flex-cnt fx-d-col no-mt h-100">
                    <NoFilesIcon />
                    <h4>No file to show</h4>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* <div className="url-crawl progress-container">
        <h3>Please Wait... </h3>
        <div className="line-progress wd-100">
          <div className="line-progress-bar wd-100" style={{ width: `${((progressState/100)*100)}%` }}></div>
        </div>
      </div> */}
    </div>
  );
}

export default AIBotTab_2;
