import { useState, useRef, useMemo, useContext, useEffect } from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import {
  DeleteIconBig,
  LinkIcon,
  RefreshIcon,
  PrimaryClrLinkIcon,
} from "@/assets/AiBotAssets";
import { BotContext } from "../store";
import type { Url } from "../types";
import { handleSearch } from "@/utils/handleSearch";
import ThreeDot from "@/components/loaders/ThreeDot";
import CircleLoader from "@/components/loaders/CircleLoader";

interface UrlWithId extends Url {
  id: string;
}

const URL_PER_PAGE = 100;
const PAGE_PER_WINDOW = 5;

function FetchUrlModal({
  characterUsed,
  characterLimit,
  fetchMoreUrl,
  fetchExtraUrl,
  refreshUrl,
  extraUrl,
  setExtraUrl,
  refreshLoader,
  fetchMoreLoader,
  extraLoader,
  fetchRemains,
  allTrained,
  crawlLimit,
  onClose,
  fetchModal,
}) {
  const dispatch = useDispatch();
  const [store, storeDispatch] = useContext(BotContext);
  const [searchInput, setSearchInput] = useState<string>("");
  const [searchInputPre, setSearchInputPre] = useState<string>("");
  const [page, setPage] = useState<number>(0);
  const [checkedSet, setCheckedSet] = useState<Set<string>>(new Set());
  const characterRemaining = characterLimit - characterUsed;
  const check = useRef(false);
  const [isAccordian, setIsAccordian] = useState(0);
  const toggle = (i) => {
    if (window.innerWidth <= 1280) {
      if (isAccordian === i) {
        return setIsAccordian(0);
      }
      setIsAccordian(i);
    }
  };

  useEffect(() => {
    if (/iPad|iPhone/.test(navigator.userAgent)) {
      const resSelectedWrapper = document.querySelector(
        ".delete-links-th"
      ) as HTMLElement;
      if (resSelectedWrapper) {
        resSelectedWrapper.style.right = "15px";
      }
    }
  }, []);

  useEffect(() => {
    const mainMenuElement = document.querySelector(
      ".main-menu-ai-builder"
    ) as HTMLElement;
    const shortLeftColElement = document.querySelector(
      ".short-left-col"
    ) as HTMLElement;
    if (fetchModal) {
      if (mainMenuElement) {
        mainMenuElement.style.zIndex = "0";
      }
      if (shortLeftColElement) {
        shortLeftColElement.style.zIndex = "0";
      }
    }
    return () => {
      if (mainMenuElement) {
        mainMenuElement.style.zIndex = "99";
      }
      if (shortLeftColElement) {
        shortLeftColElement.style.zIndex = "90";
      }
    };
  }, [fetchModal]);

  const {
    crawlData,
    selectedData,
    extraData,
    crawlLength,
    selectedLength,
    extraLength,
    crawlCharacterCount,
    selectedCharacterCount,
    allChecked,
  } = useMemo(() => {
    const crawlData: Array<UrlWithId> = [];
    const selectedData: Array<UrlWithId> = [];
    const extraData: Array<UrlWithId> = [];

    let crawlLength = 0;
    let selectedLength = 0;
    let extraLength = 0;

    let crawlCharacterCount = 0;
    let selectedCharacterCount = 0;
    let allChecked = true;

    store.crawlData.forEach((value, key) => {
      let searchCheck = true;
      if (searchInput && searchInput !== searchInputPre) {
        searchCheck = handleSearch(searchInput, value.url);
        setPage(0);
      }

      if (searchCheck) crawlData.push({ id: key, ...value });
      crawlCharacterCount += value.characterCount;
      crawlLength++;

      if (value.selected) {
        if (searchCheck) selectedData.push({ id: key, ...value });
        selectedCharacterCount += value.characterCount;
        selectedLength++;
      } else {
        allChecked = false;
      }
    });

    store.extraData.forEach((value, key) => {
      if (!value.selected) return;

      let searchCheck = true;
      if (searchInput && searchInput !== searchInputPre) {
        searchCheck = handleSearch(searchInput, value.url);
      }

      if (searchCheck) extraData.push({ id: key, ...value });
      selectedCharacterCount += value.characterCount;
      extraLength++;
    });

    setCheckedSet(new Set());

    return {
      crawlData,
      selectedData,
      extraData,
      crawlLength,
      selectedLength,
      extraLength,
      crawlCharacterCount,
      selectedCharacterCount,
      allChecked,
    };
  }, [store.crawlData, store.extraData, searchInput]);

  const paginatedCrawlData = useMemo(
    () => crawlData.slice(page * URL_PER_PAGE, (page + 1) * URL_PER_PAGE),
    [store.crawlData, searchInput, page]
  );

  const pagination = Array.from(
    { length: Math.ceil(crawlData.length / URL_PER_PAGE) },
    (_, index) => index
  );
  const paginationWindow = Math.floor(page / PAGE_PER_WINDOW);

  function toggleChecked(id: string) {
    if (checkedSet.has(id)) {
      checkedSet.delete(id);
    } else {
      checkedSet.add(id);
    }

    setCheckedSet(new Set(checkedSet));
  }

  function unselectAll() {
    storeDispatch({ type: "crawl-unselect" });
    storeDispatch({ type: "extra-delete-all" });
    check.current = false;
    dispatch(
      userSlice.actions.setToastPopUp({
        show: true,
        toastType: "success",
        message:
          "Selected links have been removed from training data. Retrain your bot to update it.",
      })
    );
  }

  function toggleLink(id: string) {
    const data = crawlData.find((item) => item.id === id);
    if (!data) return;

    if (
      !data.selected &&
      selectedCharacterCount + data.characterCount > characterRemaining
    ) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            "Max character limit reached. Remove other links or upgrade your plan.",
        })
      );
      return;
    }

    if (data.selected) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message:
            "Selected links have been removed from training data. Retrain your bot to update it.",
        })
      );
    }

    storeDispatch({ type: "crawl-toggle", id });
  }

  function toggleAllLink() {
    if (check.current) {
      storeDispatch({ type: "crawl-set-range", range: -1, state: false });
    } else {
      let characterCount = 0;
      let counter = 0;
      for (const data of crawlData) {
        if (characterCount + data.characterCount > characterRemaining) {
          dispatch(
            userSlice.actions.setToastPopUp({
              show: true,
              toastType: "error",
              message:
                "Maximum character limit has been reached. Only data containing characters within the plan limit will be used for training.",
            })
          );
          break;
        }
        characterCount += data.characterCount;
        counter++;
      }

      storeDispatch({ type: "crawl-set-range", range: counter, state: true });
    }
    check.current = !check.current;
  }

  function handleSearchInput(event: React.ChangeEvent<HTMLInputElement>) {
    setSearchInputPre(searchInput);
    setSearchInput(event.target.value);
  }

  function handlePagination(direction: "next" | "back") {
    if (
      direction === "next" &&
      (paginationWindow + 1) * PAGE_PER_WINDOW < pagination.length - 1
    ) {
      setPage((paginationWindow + 1) * PAGE_PER_WINDOW);
    }
    if (direction === "back" && (paginationWindow - 1) * PAGE_PER_WINDOW > -1) {
      setPage((paginationWindow - 1) * PAGE_PER_WINDOW);
    }
  }

  return (
    <div className="createbot-modal data-source-modal">
      <div
        className="linkPopup-container link-modal-wrapper"
        onClick={(e) => e.stopPropagation()}
      >
        <div
          className={`close flex-cnt ${isAccordian === 1 ? "close-top" : ""}`}
          onClick={onClose}
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="#6D6D6D"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
              fill="#6D6D6D"
            />
          </svg>
        </div>
        <div className="linkPopup-header fx p-relative jstfy-cnt w-link-header">
          <div className="flex-cnt gap-10">
            <svg
              onClick={onClose}
              className="cp"
              width="16"
              height="17"
              viewBox="0 0 16 17"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <g clipPath="url(#clip0_5706_10383)">
                <path
                  d="M14 6.61616V8.38384H3.39394L8.25505 13.2449L7 14.5L0 7.5L7 0.5L8.25505 1.75505L3.39394 6.61616H14Z"
                  fill="#6D6D6D"
                />
              </g>
              <defs>
                <clipPath id="clip0_5706_10383">
                  <rect
                    width="16"
                    height="16"
                    fill="white"
                    transform="translate(0 0.5)"
                  />
                </clipPath>
              </defs>
            </svg>
            <h2>Fetched Links</h2>
          </div>

          <div className="search-link fx">
            <MagnifyIcon />
            <input
              type="text"
              placeholder="Search your link"
              value={searchInput}
              onChange={handleSearchInput}
            />
          </div>
        </div>
        <div
          className={
            isAccordian === 0
              ? "linkPopup-main fx link-main-border show-acc"
              : "linkPopup-main fx link-main-border"
          }
        >
          <div className="link-main-wrapper">
            <div
              className={`linkPopup-header  p-relative jstfy-cnt m-link-header ${
                isAccordian === 1 ? "none" : ""
              }`}
            >
              <div className="flex-cnt gap-10">
                {/* <svg
                  onClick={onClose}
                  className="cp"
                  width="16"
                  height="17"
                  viewBox="0 0 16 17"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g clipPath="url(#clip0_5706_10383)">
                    <path
                      d="M14 6.61616V8.38384H3.39394L8.25505 13.2449L7 14.5L0 7.5L7 0.5L8.25505 1.75505L3.39394 6.61616H14Z"
                      fill="#6D6D6D"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_5706_10383">
                      <rect width="16" height="16" fill="white" transform="translate(0 0.5)" />
                    </clipPath>
                  </defs>
                </svg> */}
                <h2>Fetched Links</h2>
              </div>

              <div className="search-link fx">
                <MagnifyIcon />
                <input
                  type="text"
                  placeholder="Search your link"
                  value={searchInput}
                  onChange={handleSearchInput}
                />
              </div>
            </div>
            {crawlData.length ? (
              <table className="links-table table-links fetched-links-table">
                <tr
                  className={`links-table-header linkstable-head al-fx-cnt ${
                    isAccordian === 1 ? "m-fetch-head" : ""
                  }`}
                  onClick={() => isAccordian === 1 && toggle(1)}
                >
                  <th className="checkbox_th">
                    <label className="checkbox-container link-mark w-inc">
                      <input
                        className="bot-check"
                        type="checkbox"
                        checked={allChecked}
                        onChange={toggleAllLink}
                      />
                      <span className="mark">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 16 16"
                          fill="none"
                        >
                          <rect width="16" height="16" rx="5" fill="#FB5F66" />
                          <path
                            d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                            fill="white"
                          />
                        </svg>
                      </span>
                    </label>
                  </th>
                  <th className="fx fx-end mg-right g-6 no-mt al-fx-cnt">
                    Fetched Links{" "}
                    <span style={{ color: "#979797" }}>
                      {" (Total Fetched Links: " + crawlLength + ")"}
                    </span>
                  </th>
                  <th className={`open_acc ${isAccordian === 0 ? "none" : ""}`}>
                    <svg
                      width="14"
                      height="9"
                      viewBox="0 0 14 9"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M7.88384 3.76644L6.11616 3.76644L1.25505 8.62755L8.34742e-08 7.3725L7 0.372498L14 7.3725L12.7449 8.62755L7.88384 3.76644Z"
                        fill="#6D6D6D"
                      />
                    </svg>
                  </th>
                  {characterRemaining > 0 ? (
                    <th
                      className={`fx fx-d-col characters_th g-6 no-m ${
                        isAccordian === 1 ? "none" : ""
                      }`}
                    >
                      Characters{" "}
                      <span style={{ color: "#979797" }}>
                        {crawlCharacterCount}
                      </span>
                    </th>
                  ) : (
                    <th>Characters Limit Exceeded</th>
                  )}
                  {/* <th>
                    {!!selectedLength && (
                      <button className="delete-links" onClick={() => storeDispatch({ type: "crawl-delete-all" })}>
                        <DeleteIconBig />
                      </button>
                    )}
                  </th> */}
                </tr>
                <tr className="refresh_tr">
                  <td className="fs-0">
                    <LinkIcon />
                  </td>
                  <td className="ovr-flw-x-au">
                    <p>{store.url}</p>
                  </td>
                  <td className="refresh_td cp fs-0" onClick={refreshUrl}>
                    {refreshLoader ? (
                      <CircleLoader reverseColor={true} />
                    ) : (
                      <RefreshIcon />
                    )}
                  </td>
                </tr>
                <div
                  className={`table-scroll ${isAccordian === 1 ? "none" : ""}`}
                >
                  {paginatedCrawlData.map((data) => (
                    <tr className="links-table-data links_data" key={data.id}>
                      <td className="ai-self-start">
                        <label className="checkbox-container link-mark">
                          <input
                            className="bot-check"
                            type="checkbox"
                            checked={data.selected}
                            onChange={() => toggleLink(data.id)}
                          />
                          <span className="mark">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              width="16"
                              height="16"
                              viewBox="0 0 16 16"
                              fill="none"
                            >
                              <rect
                                width="16"
                                height="16"
                                rx="5"
                                fill="#FB5F66"
                              />
                              <path
                                d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                                fill="white"
                              />
                            </svg>
                          </span>
                        </label>
                      </td>
                      {/* <td><input type="text" value={data.link} className="crawled-links"/></td> */}
                      <td className="link-container-td">
                        <div
                          className={`link-container ${
                            data.url.length > 90 && "onHover"
                          }`}
                        >
                          <p className="link">{data.url}</p>
                        </div>
                      </td>
                      <td className="characters_td">{data.characterCount}</td>
                      {/* <td>
                        <button
                          className="delete-button"
                          onClick={() =>
                            storeDispatch({
                              type: "crawl-delete",
                              id: data.id,
                            })
                          }
                        >
                          <DeleteIconBig />
                        </button>
                      </td> */}
                    </tr>
                  ))}
                </div>
                <tfoot className={isAccordian === 1 ? "none" : ""}>
                  <tr className=" jstfy-sp-btw gap-20">
                    <td>
                      {/* <span className="w-mc">{`${paginatedCrawlData.length} of ${crawlLength}`}</span> */}
                    </td>
                    <td>
                      <ul className="pagination">
                        <svg
                          width="6"
                          height="12"
                          viewBox="0 0 6 12"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                          onClick={() => handlePagination("back")}
                          className={paginationWindow === 0 ? "disable" : ""}
                        >
                          <path
                            d="M5.5 11.5L0 6L5.5 0.5L5.5 11.5Z"
                            fill="#666666"
                          />{" "}
                        </svg>

                        {page >= 5 && <p>...</p>}
                        {pagination
                          .slice(
                            paginationWindow * PAGE_PER_WINDOW,
                            (paginationWindow + 1) * PAGE_PER_WINDOW
                          )
                          .map((pageNumber) => (
                            <li
                              className={page === pageNumber ? "active" : ""}
                              key={pageNumber}
                              onClick={() => setPage(pageNumber)}
                            >
                              {pageNumber + 1}
                            </li>
                          ))}
                        {!!pagination.slice(
                          (paginationWindow + 1) * PAGE_PER_WINDOW
                        ).length && <p>...</p>}

                        <svg
                          width="6"
                          height="12"
                          viewBox="0 0 6 12"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                          onClick={() => handlePagination("next")}
                          className={
                            paginationWindow ===
                              Math.floor(pagination.length / PAGE_PER_WINDOW) -
                                1 ||
                            Math.floor(pagination.length / PAGE_PER_WINDOW) ===
                              0
                              ? "disable"
                              : ""
                          }
                        >
                          <path
                            d="M0.5 0.5L6 6L0.5 11.5L0.5 0.5Z"
                            fill="#444444"
                          />
                        </svg>
                      </ul>
                    </td>
                    {/* <td className={fetchRemains && crawlLength < crawlLimit ? "fx gap-10 cp" : "fx gap-10 cp btn-disable"}> */}
                    {/* <span onClick={() => fetchRemains && crawlLength < crawlLimit && fetchMoreUrl()}>Fetch More Links</span> */}
                    <td
                      onClick={() => fetchRemains && fetchMoreUrl()}
                      className={
                        fetchRemains
                          ? "fx gap-10 cp"
                          : "fx gap-10 cp btn-disable"
                      }
                    >
                      {fetchMoreLoader ? (
                        <CircleLoader />
                      ) : (
                        <PrimaryClrLinkIcon />
                      )}
                      <span>Fetch More Links</span>
                    </td>
                  </tr>
                </tfoot>
              </table>
            ) : (
              <div className="no-links-found">
                <img
                  src="https://dlvkyia8i4zmz.cloudfront.net/XkSd2WcLQuyhxKMBQ9Jg_Group_14507.svg"
                  alt=""
                />
                <span className="fx al-cnt">
                  {allTrained ? "All links already trained" : "No links found"}{" "}
                </span>
              </div>
            )}
          </div>
          <div
            className={`link-main-wrapper selected-link-wrapper ${
              isAccordian === 1 ? "res-selected-wrapper" : ""
            }`}
          >
            <div className="linkPopup-header  p-relative jstfy-cnt m-link-header">
              <div className="flex-cnt gap-10">
                {/* <svg
                  onClick={onClose}
                  className="cp"
                  width="16"
                  height="17"
                  viewBox="0 0 16 17"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g clipPath="url(#clip0_5706_10383)">
                    <path
                      d="M14 6.61616V8.38384H3.39394L8.25505 13.2449L7 14.5L0 7.5L7 0.5L8.25505 1.75505L3.39394 6.61616H14Z"
                      fill="#6D6D6D"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_5706_10383">
                      <rect width="16" height="16" fill="white" transform="translate(0 0.5)" />
                    </clipPath>
                  </defs>
                </svg> */}
                <h2>Fetched Links</h2>
              </div>

              <div className="search-link fx">
                <MagnifyIcon />
                <input
                  type="text"
                  placeholder="Search your link"
                  value={searchInput}
                  onChange={handleSearchInput}
                />
              </div>
            </div>
            <table className="links-table table-links selected-links-table">
              <tr
                className="links-table-header linkstable-head"
                onClick={() => isAccordian === 0 && toggle(1)}
              >
                <th className="none">
                  {/* <label className="checkbox-container link-mark">
                    <input
                      className="bot-check"
                      type="checkbox"
                      checked={!!selectedData.length && checkedSet.size >= selectedData.length}
                      onChange={toggleAllChecked}
                    />
                    <span className="mark">
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
                        <rect width="16" height="16" rx="5" fill="#FB5F66"/>
                        <path d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z" fill="white"/>
                      </svg>
                    </span>
                  </label> */}
                </th>
                <th className="fx fx-end mg-right g-6 no-mt ">
                  <p className="w-head"> Selected Fetched Links</p>
                  <p className="m-head fw-500">Selected Links</p>

                  <span style={{ color: "#979797" }} className="total-links">
                    {" (Total Selected Links: " +
                      (selectedLength + extraLength) +
                      ")"}
                  </span>
                </th>
                <th className="open_acc">
                  <svg
                    width="14"
                    height="9"
                    viewBox="0 0 14 9"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M7.88384 3.76644L6.11616 3.76644L1.25505 8.62755L8.34742e-08 7.3725L7 0.372498L14 7.3725L12.7449 8.62755L7.88384 3.76644Z"
                      fill="#6D6D6D"
                    />
                  </svg>
                </th>
                {characterRemaining > 0 ? (
                  <th className="fx fx-d-col characters_th g-6">
                    <p className="fw-500">Characters</p>{" "}
                    <span
                      style={{ color: "#979797" }}
                    >{`${selectedCharacterCount}/${characterRemaining}`}</span>
                  </th>
                ) : (
                  <th>Characters Limit Exceeded</th>
                )}
                <th
                  className={` delete-links-th ${
                    !selectedData.length && "no-selectedData"
                  }`}
                >
                  <button className="delete-links" onClick={unselectAll}>
                    <DeleteIconBig />
                    {selectedData.length !== 0 && <span>Delete All</span>}
                  </button>
                </th>
              </tr>
              <div
                className={
                  isAccordian === 1 ? "table-scroll show-acc" : "table-scroll"
                }
              >
                {selectedData.length || extraData.length ? (
                  <>
                    {selectedData.map((data) => (
                      <tr
                        className={`links-table-data links_data`}
                        key={data.id}
                      >
                        <td className="none">
                          {/* <label className="checkbox-container link-mark">
                        <input
                          className="bot-check"
                          type="checkbox"
                          checked={checkedSet.has(data.id)}
                          onClick={() => toggleChecked(data.id)}
                        />
                        <span className="mark">
                          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
                            <rect width="16" height="16" rx="5" fill="#FB5F66"/>
                            <path d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z" fill="white"/>
                          </svg>
                        </span>
                      </label> */}
                        </td>
                        {/* <td><input type="text" value={data.link} className="crawled-links"/></td> */}
                        <td>
                          <div
                            className={`link-container ${
                              data.url.length > 90 && "onHover"
                            }`}
                          >
                            <p className="link">{data.url}</p>
                          </div>
                        </td>
                        <td className="characters_td">{data.characterCount}</td>
                        <td className="delete-btn-td">
                          <button
                            className="delete-button"
                            onClick={() => toggleLink(data.id)}
                          >
                            <DeleteIconBig />
                          </button>
                        </td>
                      </tr>
                    ))}
                    {extraData.map((data) => (
                      <tr className="links-table-data links_data" key={data.id}>
                        <td className="none">
                          {/* <label className="checkbox-container">
                        <input
                          className="bot-check"
                          type="checkbox"
                          checked={checkedSet.has(data.id)}
                          onClick={() => toggleChecked(data.id)}
                        />
                        <span className="mark">
                          <svg width="14" height="12" viewBox="0 0 14 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                              d="M14.0002 1.85403L4.80016 11.25L0.583496 6.94351L1.6645 5.83949L4.80016 9.03412L12.9192 0.75L14.0002 1.85403Z"
                              fill="white"
                            />
                          </svg>
                        </span>
                      </label> */}
                        </td>
                        {/* <td><input type="text" value={data.link} className="crawled-links"/></td> */}
                        <td>
                          <div
                            className={`link-container ${
                              data.url.length > 90 && "onHover"
                            }`}
                          >
                            <p className="link">{data.url}</p>
                          </div>
                        </td>
                        <td className="characters_td">{data.characterCount}</td>
                        <td className="delete-btn-td">
                          <button
                            className="delete-button"
                            onClick={() =>
                              storeDispatch({
                                type: "extra-delete",
                                id: data.id,
                              })
                            }
                          >
                            <DeleteIconBig />
                          </button>
                        </td>
                      </tr>
                    ))}
                  </>
                ) : (
                  <div className="no-links-found">
                    <img
                      src="https://dlvkyia8i4zmz.cloudfront.net/XkSd2WcLQuyhxKMBQ9Jg_Group_14507.svg"
                      alt=""
                    />
                    <span className="fx al-cnt">
                      {selectedLength || extraLength
                        ? "No links found"
                        : "No links selected"}
                    </span>
                  </div>
                )}
              </div>
              <tfoot>
                <tr className="fx jstfy-sp-btw addlink_tr">
                  <td className="fx gap-20 link_td">
                    <LinkIcon />
                    <input
                      type="text"
                      className="link_input"
                      placeholder="Enter link to add"
                      value={extraUrl}
                      onChange={(event) => setExtraUrl(event.target.value)}
                    />
                  </td>
                  <td className="addlink_td">
                    <button
                      className="add-links"
                      onClick={() => fetchExtraUrl(selectedCharacterCount)}
                    >
                      {extraLoader ? (
                        <div className="fx al-fx-end jstfy-cnt gap-6">
                          Add
                          <ThreeDot />
                        </div>
                      ) : (
                        "Add"
                      )}
                    </button>
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
        <div className="linkPopup-footer wd-100 flex-cnt">
          <button
            className={selectedData.length ? "" : "fetch-disable"}
            onClick={onClose}
          >
            Save Links
          </button>
        </div>
      </div>
    </div>
  );
}

export default FetchUrlModal;

