import { useContext } from "react";
import { useMutation } from "@tanstack/react-query";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { AxiosError } from "axios";
import { BotContext } from "../store";

export default function useBotPreview() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [store, _] = useContext(BotContext);

  return useMutation({
    mutationKey: ["bot", "previewBot"],
    mutationFn: async () => {
      const isTrained =
        store.botData.aiSource?.urls?.length ||
        store.botData.aiSource?.documents?.length;

      if (!isTrained) {
        throw new Error("Please train the bot before setting it live");
      }

      return store.botData.url;
    },
    onSuccess: async (url) => {
      navigate(`/preview/${url}`);
    },
    onError: (error) => {
      const errMessage =
        error instanceof AxiosError
          ? error.response?.data.message
          : error.message;

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: errMessage || "Something went wrong",
        })
      );
    },
  });
}
