import { useContext } from "react";
import { useMutation } from "@tanstack/react-query";
import { AxiosError } from "axios";
import { axiosPost } from "@/services/axiosService";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";
import { State } from "@/redux/Types";
import { BotContext } from "../store";
import * as GtmService from "@/services/gtmService";

export default function useBotPublish() {
  const dispatch = useDispatch();
  const [store, storeDispatch] = useContext(BotContext);
  const userId = useSelector((state: State) => state.user.userId);
  const userEmail = useSelector((state: State) => state.user.userEmail);
  const compDetails = useSelector((state: State) => state.user.companyDetails);

  return useMutation({
    mutationKey: ["bot", "publishBot"],
    mutationFn: async () => {
      const isTrained =
        store.botData.aiSource?.urls?.length ||
        store.botData.aiSource?.documents?.length;

      if (!isTrained)
        throw new Error("Please train the bot before setting it live");

      const response = await axiosPost("/users/publishBot", {
        botId: store.botData._id,
      });

      const publishData = response.data.data;
      storeDispatch({
        type: "set-botData",
        botData: {
          ...store.botData,
          liveBotId: publishData.newBotId,
          lastPublishTime: publishData.lastPublishTime,
        },
      });

      triggerGtm({
        user_email: userEmail ?? "",
        user_id: userId ?? "",
        company_id: compDetails._id ?? "",
        company_name: compDetails.compName ?? "",
        bot_id: store.botData.botCustomId ?? "",
        url: store.botData.url ?? "",
      });

      return publishData;
    },
    onSuccess: async (botData) => {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message: botData.newPublished
            ? "Your bot is now live!"
            : "Changes published successfully",
        }),
      );
    },
    onError: (error) => {
      const errMessage =
        error instanceof AxiosError
          ? error.response?.data.message
          : error.message;

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: errMessage || "Something went wrong",
        }),
      );
    },
  });
}

function triggerGtm(data: Record<string, string>) {
  GtmService.pushToDataLayer({
    event: "chatbot_launched",
    ...data,
  });
}
