import { useState, useMemo, useEffect } from "react";
import { userSlice } from "../../../redux/User";
import { useDispatch } from "react-redux";
import DeleteAlert from "./DeleteAlert";
import { DeleteIconBig } from "./AiBotAssets";
import { axiosPost } from "../../../services/axiosService";
import ThreeDot from "../../../components/loaders/ThreeDot";
import { handleSearch } from "../../../utils/handleSearch";

interface TrainData {
  url: string;
  characterCount: number;
  selected?: boolean;
}

function DeleteUrlModal({
  trainedUrls,
  botCustomId,
  botCreatedAt,
  characterUsed,
  characterLimit,
  onClose,
  onReload,
  deleteModal,
}) {
  const dispatch = useDispatch();
  const [deletePopup, setDeletePopup] = useState<boolean>(false);
  const [deleteLoader, setDeleteLoader] = useState<boolean>(false);
  const [searchInput, setSearchInput] = useState<string>("");
  const [trainedData, setTrainedData] = useState<Array<TrainData>>([]);
  const { searchTrainData, allSelected } = useMemo(() => {
    const searchTrainData: Array<TrainData> = [];
    let allSelected: boolean = true;

    for (const data of trainedData) {
      if (handleSearch(searchInput, data.url)) {
        searchTrainData.push(data);
      }
      if (!data.selected) {
        allSelected = false;
      }
    }

    return { searchTrainData, allSelected };
  }, [searchInput, trainedData]);

  const characterCount = useMemo(
    () => trainedData.reduce((pre, data) => pre + data.characterCount, 0),
    [trainedData.length]
  );

  useEffect(() => {
    const trainedData: TrainData[] = [];
    for (const data of trainedUrls) {
      trainedData.push({
        url: data.url ?? data.link,
        characterCount: data.characterCount ?? data.characters,
      });
    }

    setTrainedData(trainedData);
  }, [trainedUrls]);

  useEffect(() => {
    const mainMenuElement = document.querySelector(
      ".main-menu-ai-builder"
    ) as HTMLElement;
    const shortLeftColElement = document.querySelector(
      ".short-left-col"
    ) as HTMLElement;
    if (deleteModal) {
      if (mainMenuElement) {
        mainMenuElement.style.zIndex = "0";
      }
      if (shortLeftColElement) {
        shortLeftColElement.style.zIndex = "0";
      }
    }
    return () => {
      if (mainMenuElement) {
        mainMenuElement.style.zIndex = "99";
      }
      if (shortLeftColElement) {
        shortLeftColElement.style.zIndex = "90";
      }
    };
  }, [deleteModal]);

  function toggleUrl(urlIndex: number) {
    const temp = trainedData.map((data, index) => {
      if (index === urlIndex) {
        return {
          ...data,
          selected: !data.selected,
        };
      }
      return data;
    });
    setTrainedData(temp);
  }

  function toggleAllUrl() {
    const temp = trainedData.map((data) => ({
      ...data,
      selected: !allSelected,
    }));
    setTrainedData(temp);
  }

  async function deleteSelectedUrls() {
    const selectedUrls: string[] = [];
    for (const data of trainedData) {
      if (data.selected) selectedUrls.push(data.url);
    }
    await deleteUrls(selectedUrls);
  }

  async function deleteUrls(urls: string[]) {
    try {
      if (deleteLoader) return;

      setDeleteLoader(true);
      const res = await axiosPost("/ai/delete-content", {
        sources: urls,
        botCustomId,
        botCreatedAt,
        sourceType: "urls",
      });

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message:
            window.screen.width >= 1280
              ? res.data.message
              : "Link has been deleted successfully.",
        })
      );
      onReload();
      setDeleteLoader(false);
      setDeletePopup(false);
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: err.message,
        })
      );
      setDeleteLoader(false);
      setDeletePopup(false);
    }
  }

  return (
    <div className="createbot-modal data-source-modal">
      <div className="linkPopup-container del-links-wrapper delete-selected-wrapper">
        <div className="close flex-cnt" onClick={onClose}>
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="#6D6D6D"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
              fill="#6D6D6D"
            />
          </svg>
        </div>
        <div className="linkPopup-header">
          <div className="flex-cnt gap-10">
            {/* <svg onClick={onClose} className="cp" width="16" height="17" viewBox="0 0 16 17" fill="none" xmlns="http://www.w3.org/2000/svg">
              <g clipPath="url(#clip0_5706_10383)">
                <path
                  d="M14 6.61616V8.38384H3.39394L8.25505 13.2449L7 14.5L0 7.5L7 0.5L8.25505 1.75505L3.39394 6.61616H14Z"
                  fill="#6D6D6D"
                />
              </g>
              <defs>
                <clipPath id="clip0_5706_10383">
                  <rect width="16" height="16" fill="white" transform="translate(0 0.5)" />
                </clipPath>
              </defs>
            </svg> */}
            <h2>Fetched Links</h2>
          </div>
          <div className="search-link fx">
            <MagnifyIcon />
            <input
              type="text"
              placeholder="Search your link"
              value={searchInput}
              onChange={(event) => setSearchInput(event.target.value)}
            />
          </div>
        </div>
        <div className="linkPopup-main">
          {searchTrainData.length ? (
            <table className="links-table table-links d-block delete-selected-links">
              <tr className="links-table-header linkstable-head">
                <th className="checkbox_th">
                  <label className="checkbox-container link-mark w-inc">
                    <input
                      className="bot-check"
                      type="checkbox"
                      checked={allSelected}
                      onChange={toggleAllUrl}
                    />
                    <span className="mark">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="16"
                        height="16"
                        viewBox="0 0 16 16"
                        fill="none"
                      >
                        <rect width="16" height="16" rx="5" fill="#FB5F66" />
                        <path
                          d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                          fill="white"
                        />
                      </svg>
                    </span>
                  </label>
                </th>
                <th className="fx fx-end mg-right g-6 no-mt">
                  Trained Links{" "}
                  <span
                    style={{ color: "#8C8C8C" }}
                    className=" w-head total-links"
                  >
                    {" (Total Trained Links: " + trainedData.length + ")"}
                  </span>
                  <span
                    style={{ color: "#8C8C8C" }}
                    className="total-links m-head"
                  >
                    {" (Total Links: " + trainedData.length + ")"}
                  </span>
                </th>
                <th className="fx fx-d-col characters_th g-6">
                  Characters{" "}
                  <span
                    style={{ color: "#979797" }}
                    className="char_count"
                  >{`${characterCount}/${Math.max(
                    characterLimit - characterUsed,
                    0
                  )}`}</span>
                </th>
                {/* <th className="delete-links-th">
                  <button className="delete-links">
                    <DeleteIconBig />
                    <span>Delete All</span>
                  </button>
                </th> */}
              </tr>
              <div className="table-scroll ">
                {searchTrainData.map((data, index) => (
                  <tr className="links-table-data links_data" key={data.url}>
                    <td className="ai-self-start">
                      <label className="checkbox-container link-mark">
                        <input
                          className="bot-check"
                          type="checkbox"
                          checked={!!data.selected}
                          onChange={() => toggleUrl(index)}
                        />
                        <span className="mark">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="16"
                            height="16"
                            viewBox="0 0 16 16"
                            fill="none"
                          >
                            <rect
                              width="16"
                              height="16"
                              rx="5"
                              fill="#FB5F66"
                            />
                            <path
                              d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                              fill="white"
                            />
                          </svg>
                        </span>
                      </label>
                    </td>
                    <td className="link-container-td">
                      <div
                        className={
                          data.url.length > 90
                            ? "link-container onHover"
                            : "link-container"
                        }
                      >
                        <p className="link">{data.url}</p>
                      </div>
                    </td>
                    <td className="characters_td">{data.characterCount}</td>
                    <td className="delete-btn-td">
                      <button
                        className="delete-button"
                        onClick={() => deleteUrls([data.url])}
                      >
                        <DeleteIconBig />
                      </button>
                    </td>
                  </tr>
                ))}
              </div>
              {/* <tfoot className="d-block">
                <tr className="fx jstfy-sp-btw addlink_tr no-mb">
                  <td className="fx gap-20 link_td">
                    <LinkIcon />
                    <input
                      type="text"
                      className="link_input"
                      placeholder="Enter link to add"
                      // value={extraUrl}
                      // onChange={(event) => setExtraUrl(event.target.value)}
                    />
                  </td>
                  <td className="addlink_td">
                    <button className="add-links">Add </button>
                  </td>
                </tr>
              </tfoot> */}
            </table>
          ) : (
            <div className="no-links-found">
              <img
                src="https://dlvkyia8i4zmz.cloudfront.net/JmDg32r4RvO83hcFBb7N_no_link_found_new.svg"
                alt=""
              />
              <span>No links Found</span>
            </div>
          )}
        </div>
        <div className="linkPopup-footer center">
          <button
            className={
              trainedData.some((data) => data.selected) ? "" : "fetch-disable"
            }
            onClick={() =>
              trainedData.some((data) => data.selected) && setDeletePopup(true)
            }
          >
            {deleteLoader ? (
              <div className="fx al-fx-end jstfy-cnt gap-6">
                Delete
                <ThreeDot />
              </div>
            ) : (
              "Delete"
            )}
          </button>
        </div>
        {deletePopup && (
          <div className="inner-modal-delete">
            <DeleteAlert
              loader={deleteLoader}
              onDelete={deleteSelectedUrls}
              onClose={() => setDeletePopup(false)}
            />
          </div>
        )}
      </div>
    </div>
  );
}

export default DeleteUrlModal;

const MagnifyIcon = () => (
  <svg
    width="14"
    height="15"
    viewBox="0 0 14 15"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <g clipPath="url(#clip0_256_3554)">
      <path
        d="M5.2 0.5C6.57913 0.5 7.90177 1.04786 8.87696 2.02304C9.85215 2.99823 10.4 4.32087 10.4 5.7C10.4 6.988 9.928 8.172 9.152 9.084L9.368 9.3H10L14 13.3L12.8 14.5L8.8 10.5V9.868L8.584 9.652C7.64044 10.4574 6.44057 10.8999 5.2 10.9C3.82087 10.9 2.49823 10.3521 1.52304 9.37696C0.547855 8.40177 0 7.07913 0 5.7C0 4.32087 0.547855 2.99823 1.52304 2.02304C2.49823 1.04786 3.82087 0.5 5.2 0.5ZM5.2 2.1C3.2 2.1 1.6 3.7 1.6 5.7C1.6 7.7 3.2 9.3 5.2 9.3C7.2 9.3 8.8 7.7 8.8 5.7C8.8 3.7 7.2 2.1 5.2 2.1Z"
        fill="black"
        fillOpacity="0.5"
      />
    </g>
    <defs>
      <clipPath id="clip0_256_3554">
        <rect
          width="14"
          height="14"
          fill="white"
          transform="translate(0 0.5)"
        />
      </clipPath>
    </defs>
  </svg>
);
